.spinner-main-custom {
  position: relative;
  width: 20%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.loading-screen {
  position: fixed;
  background-color: rgba(100, 100, 111, 0.2);
  backdrop-filter: blur(8px);
  width: 100%;
  height: 100%;
  z-index: 1;
}

.navbar {
  background-color: #060b26;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

@media only screen and (max-width: 600px) {
  .nav-menu {
    background-color: #060b26!important;
    width: 250px!important;
    height: 100vh!important;
    display: flex!important;
    justify-content: center!important;
    position: fixed!important;
    top: 0!important;
    left: -100%!important;
    transition: 850ms!important;
  }

  .nav-menu.active {
    left: 0!important;
    transition: 350ms!important;
  }
}

.nav-menu {
  background-color: #060b26;
  width: 100px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  width: 250px;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 0px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.text-none {
  display: none;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

span {
  margin-left: 16px;
}

.active-menu a{
  background: #1a83ff;
  font-weight: bold;
}